html {
  box-sizing: border-box;
  font-size: 62.5%;
  min-height: 100vh;
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
  background-color: rgb(41, 41, 41);
}
body.no-scroll {
  overflow: hidden;
}

main {
  min-height: 100vh;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.4rem;
}

h1,
h2,
h3,
h4 {
  margin: 4rem 0 0 0;
  color: rgb(255, 255, 255);
}

a {
  text-decoration: none;
}

p,
a,
li {
  font-size: 2rem;
  color: rgb(255, 255, 255);
  line-height: 1.6;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
legend,
label {
  display: block;
}

blockquote {
  color: rgb(255, 255, 255);
  font-size: 1.8rem;
  margin: 2rem 0 0 2rem;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.seccion {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.contenido-centrado {
  max-width: 80rem;
  width: 95%;
  margin: 0 auto;
}

.nombre-pagina {
  color: rgb(255, 255, 255);
  font-weight: 900;
  text-align: center;
  margin-top: 4rem;
}

.descripcion-pagina {
  text-align: center;
  color: rgb(213, 213, 213);
}

.opciones {
  text-align: end;
}

.navegacion {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.navegacion a {
  padding: 1rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #cccccc;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 3rem;
    width: auto;
  }
