*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

*::-webkit-scrollbar-thumb {
  background: rgb(255, 255, 255);
  border-radius: 4px;
}
*::-webkit-scrollbar-thumb:hover {
  background: rgb(213, 213, 213);
  box-shadow: 0 0 2px 1px rgb(0, 0, 0);
}
*::-webkit-scrollbar-thumb:active {
  background-color: rgb(255, 255, 255);
}

*::-webkit-scrollbar-track {
  background: rgb(41, 41, 41);
  border-radius: 4px;
}
*::-webkit-scrollbar-track:hover {
  background: rgb(124, 124, 124);
}
*::-webkit-scrollbar-track:active {
  background: rgb(213, 213, 213);
}

.alerta {
  padding: 1rem 2rem;
  text-align: center;
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: rgb(247, 44, 44);
}
.alerta.exito {
  background-color: rgb(23, 200, 97);
}

.menu {
  width: 4rem;
  height: 4rem;
  padding: 0.5rem;
  background-color: rgb(124, 124, 124);
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s all ease;
}
.menu:hover {
  cursor: pointer;
  background-color: #636363;
}
.menu:hover::before {
  background-color: #e6e6e6;
  box-shadow: 0px 1.25rem 0px 0px #e6e6e6;
}
.menu:hover::after {
  background-color: #e6e6e6;
}
.menu::before {
  content: "";
  display: block;
  transition: 0.6s all ease;
  width: 100%;
  height: 0.5rem;
  border-radius: 0.25rem;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1.25rem 0px 0px rgb(255, 255, 255);
  transform: rotate(0) translateX(0) translateY(0);
}
.menu::after {
  content: "";
  display: block;
  transition: 0.6s all ease;
  width: 100%;
  height: 0.5rem;
  border-radius: 0.25rem;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  transform: rotate(0) translateX(0) translateY(0);
}
.menu.menu-activo::before {
  transform: rotate(-45deg) translateX(-0.75rem) translateY(0.75rem);
  box-shadow: none;
}
.menu.menu-activo::before:hover {
  box-shadow: none;
}
.menu.menu-activo::after {
  transform: rotate(45deg) translateX(-1rem) translateY(-1rem);
}

.formulario {
  max-width: 70rem;
  margin: 5rem auto;
}
.formulario .campo {
  margin-bottom: 4rem;
}
.formulario .campo label {
  color: rgb(213, 213, 213);
  font-size: 2.2rem;
  font-weight: 600;
  margin-bottom: 2rem;
}
.formulario .campo input,
.formulario .campo textarea {
  display: block;
  border: none;
  color: rgb(255, 255, 255);
  background-color: rgb(124, 124, 124);
  padding: 0.75rem;
  width: 100%;
  border-radius: 1rem;
}
.formulario .campo input::placeholder,
.formulario .campo textarea::placeholder {
  color: #efefef;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 3rem 0 2rem 0;
  background: linear-gradient(to top, transparent, rgb(41, 41, 41));
}
.header .contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .navegacion {
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
}
@media (min-width: 768px) {
  .header .navegacion {
    opacity: 1;
    visibility: visible;
    width: auto;
    height: auto;
  }
}
@media (min-width: 768px) {
  .header .menu {
    display: none;
  }
}
@media (max-width: 768px) {
  .header.header-fijo {
    z-index: 10;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4);
  }
  .header.header-fijo .logo {
    display: none;
  }
  .header.header-fijo .menu {
    position: absolute;
    top: 3rem;
    right: 3rem;
  }
  .header.header-fijo .navegacion {
    opacity: 1;
    visibility: visible;
    width: 100vh;
    height: 100vw;
    align-items: center;
    justify-content: center;
  }
}

.footer {
  background-color: rgb(124, 124, 124);
}
.footer .contenedor {
  padding: 5rem 0 4rem 0;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contenedor {
    flex-direction: row;
    gap: 0;
    justify-content: space-between;
  }
}
.footer .copyrigth {
  padding: 2rem 0;
  margin: 0;
  text-align: center;
}

.contenido {
  margin-top: 0;
  display: grid;
  transition: all 0.6s ease;
}
@media (min-width: 768px) {
  .contenido {
    grid-template-columns: 1fr 2fr;
  }
  .contenido.contenido-completo {
    grid-template-columns: 100%;
  }
  .contenido.contenido-completo .contenido-principal {
    padding-left: 8rem;
    grid-column: 1/2;
  }
}
.contenido .contenido-principal {
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenido .contenido-principal {
    grid-column: 2/3;
  }
}

.sidebar {
  width: 95%;
  margin: 0 auto;
  border-bottom: 2px solid rgb(255, 255, 255);
  background-color: #101010;
  padding: 2rem;
  transition: all 300ms ease;
}
@media (min-width: 768px) {
  .sidebar {
    position: fixed;
    top: 10rem;
    left: 1rem;
    bottom: 0;
    width: 30%;
    height: 80vh;
    border-radius: 1rem;
    overflow: auto;
    border-bottom: none;
    padding-right: 2rem;
    border-right: 2px solid rgb(255, 255, 255);
  }
}
@media (min-width: 768px) {
  .sidebar.sidebar-oculto {
    width: auto;
    height: min-content;
  }
}
.sidebar.sidebar-oculto .aside-visible {
  display: none;
}
.sidebar .aside-visible {
  margin: 2rem auto;
  width: auto;
  height: auto;
  opacity: 1;
  visibility: visible;
}
.sidebar .aside-visible .opciones-aside {
  display: grid;
  gap: 2rem;
}

.usuario-perfil {
  display: grid;
  background-color: rgb(41, 41, 41);
  grid-template-columns: 1fr 2fr;
  gap: 2rem;
  padding: 1rem;
  margin: 3rem 0;
}
.usuario-perfil img {
  display: grid;
  place-content: center;
  border-radius: 50%;
  max-width: 20rem;
  max-height: 20rem;
  background-color: rgb(213, 213, 213);
}
.usuario-perfil span {
  display: grid;
  place-content: center;
}

